<template>
  <!--  -->
  <div class="newstitle">
    <h4>{{this.info.title}}</h4>
    <p>
      <span>: {{this.info.add_time | Dateformat("YYYYMMDD HH:mm:ss")}}</span>
      <span class="mui-pull-right">: {{this.info.click}}</span>
    </p>
    <hr>
    <router-view :imgs="imgs"></router-view>
    <div v-html="this.info.content"></div>
  </div>
</template>

<script>
import Dateformat from "@/filters/dateformat.js";
export default {
  props: ["info", "imgs"],
  filters: {
    Dateformat
  }
};
</script>

<style>
h4 {
  padding: 5px 0;
}
.newstitle img {
  width: 100%;
}
</style>
